/* 1. Main Layout ================================= */
* {
    box-sizing: border-box; }

body {
    background: #ffffff; }

.content {
    background: white;
    padding: 30px;
    font-family: sans-serif;
    width: 90%;
    max-width: 600px;
    margin: 30px auto;
    border-radius: 10px; }

h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 3vw;
    white-space: nowrap;
    line-height: 150%;
}

h2 {
    color: #3a3a3c;
    font-size: 2vw;
    margin: 5%;
    text-transform: uppercase;
}

h4 {
    font-size: 1.5vw;
    color: #3a3a3c;
    margin: 20% 5% 5% 5%;
    line-height: 150%;
}

h5 {
    font-size: 1.6vw;
    color: #3a3a3c;
    color: #bf1e2d;
    margin: 5%;
    line-height: 150%;
}

/* 2. Content ================================= */

header {
    position: relative;
    height: 500px;
    width: 100%;
    
    .titleBanner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 60%;
        height: 60%;
        background: #bf1e2d;

        .triangle-topleft {
            position: absolute;
            bottom: -20px;
            right: 0;
            width: 0;
            height: 0;
            border-top: 20px solid #680005;
            border-right: 20px solid transparent;
        }

        .triangle-topright {
            position: absolute;
            bottom: -20px;
            width: 0;
            height: 0;
            border-top: 20px solid #680005;
            border-left: 20px solid transparent;

        }
    }
    
    .bannerBack {
        position: absolute;
        top: 24%;
        width: 100%;
        height: 60%;
        background: #97121f;
        z-index: -1;
    }
}



.heading {
    position: relative;
    width: 100%;
    height: 250px;
    margin-bottom: 20px;

    .notice {
        position: absolute;
        width: 57%;
        height: auto;
        top: -5%;
        left: 50%;
        z-index: -1;
        transform: translate(-50%, -50%);
        background: #fbf9ec;
        border: 2px dashed #d7d3b3;
    }
}

.demands, .demands2, .demands3, .demands4 {
    position: relative;
    background: #e7e8e9;
    height: 300px;
    width: 100%;
    border-bottom: 2px solid #818285;
    border-top: 2px solid #818285;
    z-index: 0;
}

.titleText {
    position: absolute;
    left: 50%;
    width: 50%;
    height: 300px;
    border-left: 2px dashed #818285;
}

.sign {
    position: relative;
    width: 30%;
    height: 80%;
    left: 10%;
    top: -10%; 
    .body {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #ffffff;
        box-shadow: 2px 2px 4px 4px #818285;

        .title {
            position: absolute;
            top: 5%;
            left: 4%;
            background: #bf1e2d;
            height: 30%;
            width: 90%;
        }

        .stick {
            position: absolute;
            bottom: -40%;
            background-color: #c49a6c;
            height: 100%;
            width: 10%;
            left: 50%;
            z-index: -1;
        }
    }
}

.ghettos {
    position: relative;
    height: 500px;
    width: 100%;
    background: #ffffff;
    
    .building {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    
    .imageContainer {
        display: flex;
        align-items: flex-end;
        height: auto;
        margin-left: 20%;
        width: 60%;
        justify-content: space-around;
        
        .people {
            position: relative;
//            left: -100%;
        }
    }
    
    .imageContainer2 {
        display: flex;
        align-items: flex-end;
        height: 120px;
        margin-left: 20%;
        width: 60%;
        justify-content: space-around;
        
        .people2 {
            position: relative;
//            left: -100%;
        }
    }
}

.schools, .jobs, .wage {
    position: relative;
    height: 500px;
    width: 100%;
    background: #ffffff;
}
    
.schools {
      
    .badSchool {
        display: inline-block;
        float: left;
        width: 50%;
        height: auto;
        
        img {
            width: 45vw;
            margin-left: 8%;
            z-index: -1;
            
        }
        
    }
    
    .goodSchool {
        display: inline-block;
        position: relative;
        float: left;
        width: 50%;
        
        img {
            width: 20vw;
            margin-left: 25%;
            z-index: -1;
        }
        
        .schoolImg {
            position: relative;
            
            img {
                position: absolute;
                width: 27vw;
                margin-left: 25%;
                z-index: 0;
            }
            
            .flagContainer {
                position: absolute;
                left: 32%;
                height: 60%;
                z-index: 1;
                top: 2vw;
        
                .stick {
                    position: absolute;
                    left: 0%;
                    top: 0%;
                    width: 5px;
                    height: 30vh;
                    background: #3a3a3c;
                    z-index: 0;
                }
            }
        }
    }   
}

.jobs {
    
    .black, .white, .old {
        width: 100%;
        height: 30%;
        display: flex;
        align-items: flex-end;
        
        .percentage {
            width: 100px;
            height: 100%;
        }
    }
}

.flag {
    width:10vw;
    height:6vw;
    z-index: 1;
}

.flag-element {
    animation: flag 1s ease-in-out infinite alternate; 
    background: url("../img/flag.jpg");
    background-size: 10vw 100%;
    position:relative;
    height:100%;
    width:1px;
    display:inline-block;
    box-shadow:0 1px grey, 0 -1px gray;
    z-index: 1;
    margin-top: 1vw;
}

@keyframes flag {
    0% {
        top: 5%;
    }        
    100% {
        top: -5%;
    }
}